import {
  IC_FILLED_LEADERBOARD,
  IC_FILLED_WELCOME,
  Icons
} from '@true/asset';

class TypePost {
  getTypePostImage = (type: string) => {
    return {
      announcement: Icons.Filled.SPEAKER_WAVE,
      events: Icons.Filled.CALENDAR,
      update: '',
      idea: Icons.Filled.LIGHT_BULB,
      topics: Icons.Filled.DOCUMENT_TEXT,
      polls: IC_FILLED_LEADERBOARD,
      companycorevalues: Icons.Filled.FOLDER_STAR,
      companyletters: Icons.Filled.MAIL_BOX,
      styled: '',
      welcome: IC_FILLED_WELCOME,
    }[type];
  };

  getReactionText = (type: string) => {
    return {
      announcement: 'Announcement',
      events: 'Event',
      update: 'Update',
      idea: 'Idea',
      topics: 'Topic',
      polls: 'Poll',
      companycorevalues: 'Core value',
      companyletters: 'CEO Letters',
      styled: 'Update',
      welcome: 'Welcome',
    }[type];
  };
}

export default new TypePost();
